import React from 'react';
import GitHubLogo from '../images/icons/github.png';
import GitHubLogoActive from '../images/icons/githubactive.png';
import LinkedInLogo from '../images/icons/linkedin.png';
import LinkedInLogoActive from '../images/icons/linkedinactive.png';

const LinkedInClass = "linkedin";
const GithubClass = "github"

function SocialLinksComponent(){

    function handleHover(event)
    {
        let el = event.target;
        if(el.className === LinkedInClass) {
            el.setAttribute("src", LinkedInLogoActive);
        } else {
            el.setAttribute("src", GitHubLogoActive);
        }
    }

    function handleMouseOut(event){
        let el = event.target;
        if (el.className === LinkedInClass){
            el.setAttribute("src", LinkedInLogo);
        } else {
            el.setAttribute("src", GitHubLogo);
        }
    }

    return (
        <div className="profile-links">
            <a href="https://github.com/matthewskillington" target="_blank" rel="noopener noreferrer">
                <img 
                    src={GitHubLogo} 
                    className={GithubClass}
                    height="48" 
                    width="48"
                    onMouseOver={handleHover}
                    onMouseOut={handleMouseOut}
                    alt="Github Link"/>
            </a>
            <a href="https://www.linkedin.com/in/matthew-skillington-12236285/" target="_blank" rel="noopener noreferrer">
                <img 
                    src={LinkedInLogo} 
                    className={LinkedInClass}
                    height="48" 
                    width="48"
                    onMouseOver={handleHover}
                    onMouseOut={handleMouseOut}
                    alt="Linkedin Link"/>
            </a>
        </div>
    )
}

export default SocialLinksComponent;